<template>
<div>
    <el-card class="box-card">
        <el-row class="inverted-index-row">
            下载歌词的权重矩阵表：
            <a href="http://47.95.200.233/search-homework/lyric_vector_index.json"><el-button type="primary">点击下载</el-button></a>
        </el-row>

        <el-row class="inverted-index-row">
            下载歌名的权重矩阵表：
            <a href="http://47.95.200.233/search-homework/name_vector_index.json"><el-button type="primary">点击下载</el-button></a>
        </el-row>
        <el-row style="color: red" class="inverted-index-row">
            如果点击后没有下载而是跳转，请点击鼠标右键-->链接另存为
        </el-row>
        <el-row class="inverted-index-row">
            该权重矩阵基于倒排索引建立，停用词及词项ID均相同。
        </el-row>

    </el-card>
    
    <el-card>
        <el-row class="inverted-index-row">
            歌词的权重矩阵（只展示前10首）：
        </el-row>
        <el-table
            v-loading="loading"
            :data="song_lyric_index"
            border
            style="width: 100%">
                <el-table-column type="expand">
                    <template slot-scope="props" >
                        <el-form label-position="top" inline class="table-expand-form">
                            <el-form-item label="歌名">
                                <div v-html="props.row.name"></div>
                            </el-form-item>
                            <el-form-item label="歌词">
                                <div v-html="props.row.lyric"></div>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="name"
                    label="歌名"
                    width="180">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop="w_list"
                    label="权重矩阵(格式：“词项ID：tf-idf”)">
                </el-table-column>
                <el-table-column
                    prop="length"
                    label="欧氏长度"
                    width="100">
                </el-table-column>
        </el-table>
    </el-card>
    <el-card>
        <el-row class="inverted-index-row">
            歌名的权重矩阵（只展示前10首）：
        </el-row>
        <el-table
            v-loading="loading2"
            :data="song_name_index"
            border
            style="width: 100%">
                <el-table-column type="expand">
                    <template slot-scope="props" >
                        <el-form label-position="top" inline class="table-expand-form">
                            <el-form-item label="歌名">
                                <div v-html="props.row.name"></div>
                            </el-form-item>
                            <el-form-item label="歌词">
                                <div v-html="props.row.lyric"></div>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="name"
                    label="歌名"
                    width="180">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop="w_list"
                    label="权重矩阵(格式：“词项ID：tf-idf”)">
                </el-table-column>
                <el-table-column
                    prop="length"
                    label="欧氏长度"
                    width="100">
                </el-table-column>
        </el-table>
    </el-card>
</div>
</template>

<script>
import Vue from 'vue'
export default {
    data() {
        return {
            song_lyric_index: [],
            song_name_index: [],
            loading: true,
            loading2: true
        }
    },
    mounted() {
        this.get_lyric_index()
        this.get_name_index()
    },
    methods: {
        get_lyric_index(){
            this.axios.get('/search/vector-list',{
                params:{
                    kind:0
                }
            })
              .then(res => {
                for(var i=0 ; i<res.data.length;i++)
                {
                    Vue.set(this.song_lyric_index,i,{
                        name: res.data[i][4],
                        w_list: res.data[i][1],
                        length: res.data[i][2],
                        lyric: res.data[i][6]
                    })
                }
                this.loading = false
            })
        },
        get_name_index(){
            this.axios.get('/search/vector-list',{
                params:{
                    kind:1
                }
            })
              .then(res => {
                for(var i=0 ; i<res.data.length;i++)
                {
                    Vue.set(this.song_name_index,i,{
                        name: res.data[i][4],
                        w_list: res.data[i][1],
                        length: res.data[i][2],
                        lyric: res.data[i][6]
                    })
                }
                this.loading2 = false
            })
        }
    }
}
</script>


<style>

.inverted-index-row {
    margin-bottom: 10px;
    margin-top: 10px
}
.table-expand-form label {
    width: 90px;
    color: #99a9bf;
}
</style>




